@import "./helpers/HoverSupported.scss";
@import "./helpers/Media.scss";

@include hover-supported {
  .bio-part-animate,
  .projets-part-animate,
  .contact-part-animate {
    opacity: 0.5;
    transition: all ease-out 0.7s;
  }

  .bio-part-animate,
  .contact-part-animate {
    transform: translateX(-20px);
  }
  .projets-part-animate {
    transform: translateX(20px);
  }

  .is-visible {
    opacity: 1;
    transform: translateX(0);
  }
}
